.pagination {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 30px 0;
    list-style-type: none;
}

.pagination-item {
    font-size: 18px;
    width: 25px;
    height: 25px;
    margin: 5px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        cursor: pointer;
    }

    &--active {
        color: white;
        position: relative;
    }

    &--active::before {
        content: '';
        width: 30px;
        height: 30px;
        position: absolute;
        top: -4px;
        box-shadow: 0 0 5px cadetblue;
        border-radius: 50%;
        background-color: cadetblue;
        z-index: -1;
    }
}
